/* settings
=============================*/
.settings-bg{
	/* display: none; */
	position: absolute;
	top:  85px;
	left: 0;
	width: 100%;
	min-height: calc(100% - 6px);
	background: #000;
	padding: 20px 0 115px;
	border-top: 1px solid #4a4a4b;
	z-index: 100;
		.close{
			position: absolute;
			top: 20px;
			right: 20px;
			cursor: pointer;
		}
}
.settings-title{
	text-transform: uppercase;
	text-align: center;
}
.tab-items{
	margin-top: 40px;
		li{
			float: left;
			width: 33.33%;
			padding: 10px 0 5px 0;
			text-align: center;
			border: none;
			text-transform: uppercase;
			border-bottom: 1px solid #4a4a4b;
			cursor: pointer;
			font-size: 1.2rem;
				&.active{
					border: 1px solid #4a4a4b;
					border-bottom: none;
				}
				&:first-child{
					border-left: none;
				}
				&:last-child{
					border-right: none;
				}
		}
}
.tab-content{
	/* display: none; */
	padding: 50px 60px 10px;
		&.active{
			display: block;
		}
}
.informer_conf{
	font-size: 1.8rem;
	color: red;
	margin: 110px 0 0;
}
.tab-content__line{
	position: relative;
	width: 520px;
	margin-bottom: 15px;
	text-align: right;
	font-size: 14px;
		input,
		.btn{
			width: 125px;
			min-width: 125px;
			margin-left: 20px;
			height: 35px;
			line-height: 33px;
			text-align: center;
		}

}
.select-group{
	margin-bottom: 30px;
}
.net-sett{
	margin-top: 130px;
}
/* dropdown
===========================*/
.select-dropdown{
	position: relative;
	width: 300px;
	outline: none;
}
.select-dropdown.active{
		.dropdown{
			display: block;
		}
		.dropdown__toggle{
			transform: rotate(180deg);
		}
}
.selected__item{
	text-align: center;
	width: 100%;
	height: 40px;
	background: #e3e3e4;
	color: #555555;
	padding: 0 20px 0 10px;
	line-height: 39px;
	cursor: pointer;
			span{
			    display: inline-block;
			    vertical-align: middle;
			    
		}
}
.selected__name {
	width: 100%;
	position: absolute;
	text-align: center;
	left: 0;
}
.select-dropdown_flag{
	.selected__item{
		text-align: left;
	}
	.selected__name{
		text-align: right;
		padding-right: 20px;
	}
}

.selected__flag{
	width: 90px;
	height: 30px;
}
.dropdown__item{
	/* display: none; */
	margin: 0 0 1px;
	background: #c2c2c2;
}
.dropdown{
	display: none;
	position: absolute;
	top: 40px;
	left: 0;
	width: 100%;
	z-index: 15;
	background: #000;
}
.dropdown__toggle{
	position: absolute;
	right: -40px;
	top: 5px;
	width: 29px;
	height: 29px;
	background: url('../images/pic/drop_arrow.png') no-repeat;
}
.select-title{
	font-size: 1.3rem;
	display: inline-block;
	margin-bottom: 10px;
}

.footer__sett{
	position: absolute;
	width: 100%;
	bottom: 30px;
}