/* icons
==================================*/
.icon{
  display: inline-block;
  position: relative;
  top: 3px;
  background: url('../images/pic/icon_spr1.png') no-repeat;
}
.i-sett{
  width: 16px;
  height: 16px;
  margin-right: 3px;
  background-position: 0px 0px;
}

.i-exit{
	width: 22px;
	height: 16px;
	background-position: 0px -22px;
}

.i-turn{
	width: 11px;
	height: 2px;
	background-position: -25px -12px;
}

.i-close{
	width: 13px;
	height: 13px;
	background-position: -25px -24px;
}
.i-check{
	width: 30px;
	height: 29px;
	background-position: -43px -6px;
}
.i-info{
	width: 30px;
	height: 29px;
	background-position: -42px -41px;
}
.i-warning{
	width: 30px;
	height: 29px;
	background-position: -6px -41px;
}
.i-warning-tr{
	width: 30px;
	height: 30px;
	background-position: -6px -104px;
}
.i-load{
	width: 38px;
	height: 29px;
	background-position: -2px -73px;
}
.soc-icon{
	display: inline-block;
	width: 35px;
	height: 20px;
	background: url('../images/pic/soc_spr.png') no-repeat;
}
.vk{
	background-position: 0px -1px;
}
.youtube{
	background-position: -49px -1px;
}
.facebook{
	background-position: -96px -1px;
}