
/* nav menu
================================*/
.header-bot{
	padding: 25px 0 10px 0;
	font-family: "HelveticaNeueBold", Helvetica, Arial, sans-serif;
}
.nav  li{
	list-style: none;
}
.nav__item{
	float: left;
	padding: 0;
	margin: 0 35px 0 0;

}
.nav__item ul{
	display: none;
	position: absolute;
	top:  34px;
	right: 7px;
	transform: translateX(50%);
	-webkit-transform: translateX(50%);
	border:  1px solid #505050;
	padding: 0 35px;
	text-align: center;
	background: #000;
	z-index: 10;
	font-family: "HelveticaNeue", Helvetica, Arial, sans-serif;
	font-weight: normal;

}
.nav__item.active ul{
	display: block;
}
.nav__item ul:after{
	content: '';
	position: absolute;
	left: calc(50% - 8px);
	top: -9px;
	border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid black;
}

.nav__item ul:before{
	content: '';
	position: absolute;
	left: calc(50% - 8px);
	top: -11px;
	border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #505050;
}
.nav__item ul li{
	position: relative;
	padding: 10px 0;
	margin: 0;
	border-bottom: 1px solid #505050;

}
.nav__item ul li:first-child:before{
	content: '';
	top: -25px;
	left: 0;
	position: absolute;
	width: 100%;
	background: transparent;
	height: 25px;
}
.nav__item ul li a{
	white-space: nowrap;
	font-size: 1.4rem;
	padding: 0 20px;

}
.nav__item ul li:last-child{
	border: none;
}
.nav__item > a{
	color: #cccccc;
	font-size: 1.5rem;
	text-transform: uppercase;
}
.nav__item:hover > a{
	color: #018bda;
}
.nav__item:hover > ul{
	display: block;
}
.social{
	margin-top: -3px;
	color: #7c7c7c;
}
.social a{
	display: inline-block;
	vertical-align: middle;
	width: 35px;
	text-align: center;
	margin-left: 10px;
}