/* form
============================*/
.lnc-form{
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 355px;

}
.form-group{
	position: relative;
	margin-top: 30px;
		&:first-child{
			margin-top: 0;
		}
		.icon{
			display: none;
		}
}
.hidd-msg{
	display: none;
	position: absolute;
	right: 0;
	top: 50%;
	max-width: 210px;
	padding: 5px 15px;
	color: #555555;
	background: #fff;
	transform: translate(calc(100% + 55px), -50%);
	font-size: 13px;
		&:before{
			content: '';
			position: absolute;
			top: 50%;
			left: -10px;
			transform: translateY(-50%);
			width: 0;
			height: 0;
			border-right: 13px solid #fff;
			border-top: 8px solid transparent;
			border-bottom: 8px solid transparent;
		}
}
.error + .icon,
.check + .icon{
	display: inline-block;
	position: absolute;
	top: 50%;
	right: -40px;
	transform: translateY(-50%);
	width: 30px;
	height: 30px;
}
.error ~ .hidd-msg{
	display: block;
}
.error_01 + .icon{
	background-position: -6px -41px;
}
.error_02 + .icon{
	background-position: -6px -104px;
}
.error_02[type="password"] ~ .hidd-msg{
	transform: translate(calc(100% + 55px), -30%);
}
.error_02[type="password"] ~ .hidd-msg:before{
	top:  30%;
}
.check + .icon{
	background-position: -43px -6px;
}
.check ~ .hidd-msg{
	display: none;
}
.form-group input{
	width: 100%;
}
input[type="text"],
input[type="password"],
input[type="email"]{
	height: 40px;
	color: #555555;
	padding: 0 15px;
	border: none;
	background: rgba(255, 255, 255, .83);
}
button[type="submit"]{
	width: 100%;
}
input::-webkit-input-placeholder {color:#555555;}
input::-moz-placeholder          {color:#555555;}/* Firefox 19+ */
input:-moz-placeholder           {color:#555555;}/* Firefox 18- */
input:-ms-input-placeholder      {color:#555555;}

.pass-recovery{
	text-align: right;
}
.pass-recovery a{
	color: #d6d6d5;
	border-bottom: 2px dotted #d6d6d5;
	font-size: 14px;
}
.check-group{
	margin-top: 15px;
		span{
			font-size: 14px;
		}
}
.checkbox
{
    width: 17px;
    height: 17px;
    margin: 0 3px 0 0;

    vertical-align: top;
}
.checkbox + label
{
    cursor: pointer;
}
.checkbox:not(checked)
{
    position: absolute;

    opacity: 0;
}
.checkbox:not(checked) + label
{
    position: relative;

    padding: 0 0 0 35px;
}
.checkbox:not(checked) + label:before
{
    position: absolute;
    top: -1px;
    left: 0;

    width: 19px;
    height: 19px;

    content: '';

   /*  border: 2px solid #a5a4a4; */
    background: url('../images/pic/form_spr.png') no-repeat;
    background-position: -21px -37px;
}
.checkbox:checked + label:before
{
    background: url('../images/pic/form_spr.png') no-repeat;
    background-position: 0px -37px;
}

/* errors
=========================*/
.error:before{
	content: '1213';
	position: absolute;
	width: 20px;
	height: 20px;
	background: red;
}