/* download
======================*/
.download{
	width: 55%;
	font-size: 1.4rem;
}
.download__scroll{
	width: 100%;
	position: relative;
	height: 9px;
	margin-top: 5px;
	background: #222128;
	background: linear-gradient(to bottom, #47464c , #222128,  #47464c);
	background: -webkit-linear-gradient(to bottom, #47464c , #222128,  #47464c);
	background: -moz-linear-gradient(to bottom, #47464c , #222128,  #47464c);
	border-radius: 20px;
	clear: both;
}
.download__scroll span{
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	background: linear-gradient(to bottom, #7dc4ec , #018bda,  #7dc4ec);
	background: -webkit-linear-gradient(to bottom, #7dc4ec , #018bda,  #7dc4ec);
	background: -moz-linear-gradient(to bottom, #7dc4ec , #018bda,  #7dc4ec);
/* 	border-left: 2px solid #7dc4ec;
	border-right: 2px solid #7dc4ec; */
	height: 100%;
	height: 100%;
	border-radius: 20px 0 0 20px;
}
.scroll_check span{
    background: linear-gradient(to bottom, #7dc4ec , #4bd5f2,  #7dc4ec);
	background: -webkit-linear-gradient(to bottom, #7dc4ec , #4bd5f2,  #7dc4ec);
	background: -moz-linear-gradient(to bottom, #7dc4ec , #4bd5f2,  #7dc4ec);
/* 	border-left: 2px solid #7dc4ec;
	border-right: 2px solid #7dc4ec; */
}
.scroll_install span{
    background: linear-gradient(to bottom, #7dc4ec , #0333cf,  #7dc4ec);
	background: -webkit-linear-gradient(to bottom, #7dc4ec , #0333cf,  #7dc4ec);
	background: -moz-linear-gradient(to bottom, #7dc4ec , #0333cf,  #7dc4ec);
/* 	border-left: 2px solid #7dc4ec;
	border-right: 2px solid #7dc4ec; */
}
.download__scroll span.full{
	border-radius: 20px;
}