/* popup
===========================*/
.fader{
	display: none;
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	z-index: 15;
	background: rgba(0, 0, 0, .8);
}
.popup{
	position: absolute;
	min-width: 580px;
	width: 40%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	transform: -webkit-translate(-50%, -50%);
	border: 1px solid #565859;
	padding: 30px 15px;
	text-align: center;
	z-index: 999;
	background: #000;
}
.popup__icon,
.popup__text{
	display: inline-block;
	vertical-align: middle;
}
.popup__icon{
	width: 90px;
	height: 80px;
	margin-right: 40px;
}
.popup__icon.atten{
	background: url('../images/pic/attention.png') no-repeat;
	background-size: 100% 100%;

}
.popup__icon.check{
	width: 70px;
	height: 70px;
	background: url('../images/pic/check.png') no-repeat;
	background-size: 100% 100%;

}
.popup__icon.question{
	width: 42px;
	height: 69px;
	background: url('../images/pic/question.png') no-repeat;
	background-size: 100% 100%;

}
.popup__text{
	width: 70%;
	text-align: left;
	line-height: 29px;
}

.disable-area{
	width: 100%;
	height: 80px;
	background: #121112
}