/* news
==========================*/
.content__text{
	position: relative;
	z-index: 10;
	padding-left: 50px;
	margin-top: -83px;
	background: -webkit-linear-gradient(to right, #000 45%, rgba(0, 0, 0, .3) 100%);
    background: linear-gradient(to right, #000 45%, rgba(0, 0, 0, .3) 100%);
    background: -webkit-linear-gradient(to right, #000 45%, rgba(0, 0, 0, .3) 100%);
    background: -ms-linear-gradient(to right, #000 45%, rgba(0, 0, 0, .3) 100%);
}

.news__list li{
	display: block;
	position: relative;
	padding: 9px 0;
}

.news__link .fa{
	position: relative;
	margin: 0 0 0 10px;
}
.news__list li:after{
	content:  '';
	position: absolute;
	margin: 0;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 1px;
	background: -webkit-linear-gradient(to right, #555555 30%, transparent 65%);
    background: linear-gradient(to right, #555555 30%, transparent 65%);
    background: -webkit-linear-gradient(to right, #555555 30%, transparent 65%);
}
.news__list li:last-child:after{display: none;}

.news__link_all{
	color: #0178bd;
}