/* content footer
=======================*/
.footer,
.footer__sett{
	position: relative;
	z-index: 10;
	margin-top: 15px;
	padding: 0 50px;
	display: flex;
	justify-content: space-between;
	align-items: center;
		.btn{
			width: 354px;
		}
}
.btn-group{
	position: relative;
}
.btn-group.active .hide_btn{
	display: block;
}
.hide_btn{
	display: none;
	position: absolute;
	top: -40px;
	left: 0;
	z-index: 15;
}
.informer__text{
	display: inline-block;
	vertical-align: middle;
	font-size: 1.4rem;
}
.informer .icon{
	display: inline-block;
	vertical-align: middle;
	margin: -5px 30px 0 0;
}
.copyright{
	font-size: 10px;
	width: 165px;
	color: #636363;
	text-align: center;
		p:last-child{
			margin-top: 10px;
		}
}