/* BASE
===============================*/
*{
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
html {
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	font-size: 62.5%;
}

body {
	font-family: "HelveticaNeue", Helvetica, Arial, sans-serif;
	font-size: 1.6rem;
	line-height: 1.42857143;
	color: #fff;
  	height: 100vh;
  	width: 100%;
  	background: rgba(0, 0, 0, .7);
}
/* Lists
===================================*/
ul { list-style: none; }
ol { list-style: decimal inside; }

ol, ul { margin: 0; padding: 0; }

ul ul,
ul ol,
ol ol,
ol ul { margin: 10px 0 0 20px; }

li { margin: 0 0 5px; }

ol { counter-reset: ol; }
ul > li { padding-left: 2ch; }
ol > li { padding-left: 3ch; }
li {
  position: relative;
  display: block;
}

ul > li:before { content: ""; }

ol > li:before {
  content: counter(ol) ".";
  counter-increment: ol;
}
i{font-style: normal;}
/* Links
===================================*/
a {
  color: #fff;
  text-decoration: none;
  transition: color .25s linear;
}
a:hover,
a:focus{
	color: #018bda;
  	text-decoration: none;
    transition: color .25s linear;
}
a:focus {
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}
figure {
  margin: 0;
}
/* img
===================================*/
img {
  vertical-align: middle;
}

/* buttons
==============================*/
.btn,
input[type=button],
button
{
	position: relative;
	display: inline-block;
	text-align: center;
  	white-space: nowrap;
  	text-decoration: none;
	outline: none;
	border: none;
	height: 40px;
	line-height: 40px;
	min-width: 140px;
	font-weight: 400;
	cursor: pointer;
	text-transform: uppercase;

}
button[disabled]{
	background: #555555;
	color: #aaaaaa;
}
.btn_blue{
	background: #018bda;
	border: 1px solid #011c42;
}
.btn_red{
	background: #ea020b;
	border: 1px solid #022c67;
}
.btn_trans{
	background: transparent;
	border: 1px solid #737377;
	color: #fff;
}
.arrow_btn{
	position: absolute;
	top: 0;
	right: 0;
	width: 70px;
	height: 40px;
	cursor: pointer;

}
.active .arrow_btn:after{
	transform: rotate(180deg);
	-webkit-transform: rotate(180deg);
}
.arrow_btn:before{
	content: "";
	position: absolute;
	height: 30px;
	top: 5px;
	width: 1px;
	right: 65px;
	background: #fff;
	opacity: .4;
}
.arrow_btn:after{
	content: "";
	position: absolute;
	width: 25px;
	height: 13px;
	right: 20px;
	top: 13px;
	background: url('../images/pic/arrow.png') no-repeat;
	display: inline-block;
}
.arrow_btn input{
	display: none;
}
.arrow_btn input:checked + label:after{
	transform: rotate(180deg);
	-webkit-transform: rotate(180deg);
}
.hide{
	display: none;
}
.visible{
	display: block!important;
}
.pull-left{
	float: left;
}
.pull-right{
	float:  right;
}
.clearfix:after{
	content: '';
	display: table;
	clear: both;
}
.t-left{
	text-align: left;
}
.t-right{
	text-align: right;
}
.t-center{
	text-align: center;
}