/* window main settings
==========================*/
.main-sett__item{
	display: inline-block;
	vertical-align: middle;
	border-right: 1px solid #4b4b4c;
	padding: 5px 27px 5px 28px;
	font-size: 1.4rem;
	margin-left: -.3em;
}
.main-sett__item span{
	cursor: pointer;
	text-transform: uppercase;
}
.main-sett__item .settings{
	padding-left: 3px;
}
.main-sett__item .turn{
	margin-right: 16px;
}
.main-sett__item:last-child{
	padding-right: 0;
	border-right: none;
}
.main-sett__item:first-child{
	padding-left: 0;
}
.main-sett__info{
	text-transform: uppercase;
	margin-top: 20px;
	font-size: 1.4rem;
}
.main-sett__info .label{
	color: #888787;
}
.main-sett__info .info{
	margin-left: 10px;
	color: #a6a6a6;
}
/* content
===========================*/
.content-wrap{
	position: relative;
}
.bg__img{
	width: 100%;
	min-height: 410px;
	/* height: 410px; */
}
.bg__text{
	position: absolute;
    top: 29px;
    right: 5px;
    font-size: 2.8rem;
    color: #fff;
    text-transform: uppercase;
    line-height: 54px;
    width: 370px;
	font-family: "HelveticaNeueCondBold", Helvetica, Arial, sans-serif;

}